import { API } from '../../API';
//import Loader from '../Loader/Loader';

export default class Cells {
  static LoadCells() {
    const URL = "https://shubhamjagdhane.github.io/jsonfiles/pucsd/Cells/cells.json";
    const cellsId = document.getElementById("cells");
    API.get(URL)
    .then((data) => {
      var html = 
      `
      <div class="section-title text-center">
        <h3>${data.Heading}</h3>
        <p>${data.SubHeading}</p>
      </div>
      `;
      
      const IconsName = ["flaticon-seo", "flaticon-development", "flaticon-process", "flaticon-discuss-issue", "flaticon-idea"];

      html += ` <div class="main-section">`;
      
      data.Cells.forEach((cell, index) => {
        html +=
        `
        <div class="cells-container">
          <div class="services-inner-box">
            <div class="ser-icon">
              <i class="${IconsName[index]}"></i>
            </div>
            <h2>${cell.Name}</h2>
        `
        cell.Information.Paragraphs.forEach((info) => {
            html += `<p>${info}</p>`;
        })
        
        html +=
          `
            <center>
              <p>${cell.Name} ${data.Year}</p>
            `;
            cell.FacultyMembers.forEach((FacultyMember) => {
                html += `<p><i class="fa fa-user" aria-hidden="true"></i> ${FacultyMember.Name} ${FacultyMember.Post}</p>`;
            });
            
            
            html += 
              `                    
              <table>
              `;
            cell.CurrentMembers.forEach((CurrentMember) => {
              html += `
              <tr>
                  <td><p><i class="fa fa-user" aria-hidden="true"></i> ${CurrentMember.Name}</p></td>
                  <td><p>${CurrentMember.Mobile}</p></td>
              </tr>
              `;
            });

          html +=    
          `
            </table>
            </center>
            <p><i class="fa fa-map-marker" aria-hidden="true"></i></p>
            <p>${cell.Address.Cell}</p>
            <p>${cell.Address.Dept}</p>
            <p>${cell.Address.University}</p>
            <p>${cell.Address.City}- ${cell.Address.Pincode}</p>
            <p><i class="fa fa-phone-square" aria-hidden="true"></i> ${cell.Mobile}</p>
            <p><i class="fa fa-envelope" aria-hidden="true"></i> <a href="mailto:${cell.Email}">${cell.Email}</a></p>

            <div class="date">
              <span><a target="_blank" href="${cell.Webpage}"><i class="fa fa-external-link-square" aria-hidden="true"></i></a></span>
            </div>
          </div>
        </div>
      
        `;
      });

      cellsId.innerHTML = html;
    })
    //cellsId.innerHTML = Loader.SmallLoader();
  }

  static LoadContent() {
    setTimeout(() => {
      const ISOptions = {
        threshold: 0.8,
        rootMargin: "0px 0px 0px 0px"
      }
      const cellsObserver = new IntersectionObserver((entries, cellsObserver) => {
        entries.forEach((entry)=> {
          if(entry.isIntersecting) {            
            cellsObserver.unobserve(entry.target);
            Cells.LoadCells();
          }
        });
        
      },ISOptions);
      cellsObserver.observe(document.getElementById("cells"));
    }, 2000);
  }
}