import { API } from '../../API';
import Loader from '../Loader/Loader';

export default class News {
  static LoadButtons() {
    const URL = "https://shubhamjagdhane.github.io/jsonfiles/pucsd/News/buttons.json";
    const newsId = document.getElementById("news");
    let buttonsId = [];
    API.get(URL)
    .then((data) => {
      var html = 
      `
      <div class="section-title text-center">
        <h3>${data.Heading}</h3>
      </div>
      <div class="news-buttons">
      <button id="all" class="active">All</button>
      
      `
      ;
      data.Buttons.forEach((button) => {
        buttonsId.push(button.Id)
        html += `<button id="${button.Id}">${button.Label}</button>`;
      });
      
      html += `</div><div class="main-section"></div>`;
      setTimeout(() => {
        newsId.innerHTML = html;
        News.ButtonsClick(buttonsId);
      }, 1000)
      
    });
    //newsId.innerHTML = Loader.SmallLoader();
    
  }

  static ButtonsClick(buttonsId) {
    buttonsId.forEach((button) => {
      document.getElementById(button).addEventListener("click", (clickedButton) => {
        let fileName = clickedButton.target.getAttribute("id");
        if(clickedButton.target.classList.contains("active"))
          return;
        fileName = fileName.split("Button")[0]
        
        const URL = "https://shubhamjagdhane.github.io/jsonfiles/pucsd/News/"+fileName+".json";
        const newsId = document.querySelector("#news");
        const newsInfo = newsId.getElementsByClassName("main-section")[0];
        

        API.get(URL)
        .then((data) => {
          
          let html = ``;
          data.News.forEach((_new) => {
            html += 
            `
            <div class="news-container">
              <img src="${data.Image}" alt="Image">
      
              <div class="img-overlay">
      
                <div class="text">
                  <h1>${_new.Heading}</h1>
                  <p>${_new.Content}</p>
                </div>
      
                <div class="ext-link">
                  <span><a target="_blank" href="${_new.ExternalLink}"><i class="fa fa-external-link-square" aria-hidden="true"></i></a></span>
                </div>
      
              </div> <!-- img-overlay end -->
            </div>
            `;
          });

          
          const newsId = document.querySelector("#news");
          const newsInfo = newsId.getElementsByClassName("main-section")[0];
          newsInfo.innerHTML = html;
          document.querySelectorAll("#news .news-buttons button").forEach((button) => {
            if(button.classList.contains("active")) {
              button.classList.remove("active");
            }
          });
          
          clickedButton.target.classList.add("active");
          
        });

        newsInfo.innerHTML = Loader.SmallLoader();
        
      })
    });
  }

  static LoadContent() {
    setTimeout(() => {
      const ISOptions = {
        threshold: 0.8,
        rootMargin: "0px 0px 0px 0px"
      }
      const newsObserver = new IntersectionObserver((entries, newsObserver) => {
        entries.forEach((entry)=> {
          if(entry.isIntersecting) {
            newsObserver.unobserve(entry.target);
            News.LoadButtons();
          }
        });
        
      }, ISOptions);
      newsObserver.observe(document.getElementById("news"));
    }, 2000);
  }

}