import { API } from '../../API';
//import Loader from '../Loader/Loader';

export default class Student {
  static LoadStudent() {
    const studentId = document.getElementById("students");
    const URL = "https://shubhamjagdhane.github.io/jsonfiles/pucsd/Students/students.json";

    API.get(URL)
    .then((student) => {
      let html = 
      `
      <div class="student-attributes">
        <div class="section-title text-center">
          <h3>${student.Heading}</h3>
        </div>
        <div class="student-section">
          <p> ${student.Description}
            <span><a target="_blank" href="${student.Webpage}"><i class="fa fa-external-link-square" aria-hidden="true"></i></a></span>
          </p>
        </div>
      </div>
      `;

      studentId.innerHTML = html;

    });
    //studentId.innerHTML = Loader.SmallLoader();
  }

  static LoadContent() {
    setTimeout(() => {
      const ISOptions = {
        threshold: 0.8,
        rootMargin: "0px 0px 0px 0px"
      }
      const studentObserver = new IntersectionObserver((entries, studentObserver) => {
        entries.forEach((entry)=> {
          if(entry.isIntersecting) {
            studentObserver.unobserve(entry.target);
            setTimeout(Student.LoadStudent(), 5000);
            
          }
        });
        
      }, ISOptions);
      studentObserver.observe(document.getElementById("students"));
    }, 2000);
  }
}