import { API } from "../../API";
import Loader from '../Loader/Loader';

export default class Banner {
  constructor() {
    this.URL = "https://shubhamjagdhane.github.io/jsonfiles/pucsd/Headers/";
    this.sectionId = "banner-container";
  }

  LoadData() {
    const URL = this.URL + "banner.json";
    const Section = document.querySelector(".banner-container");
    API.get(URL)
    .then (data => {
      var html = `    
      <div class="heading">
        <h1 class="heading">${data.Heading}</h1>
      </div>
      <div class="sub-heading">`;

      data.SubHeading.forEach(SubHeading => {
        html += `<span>${SubHeading} </span>`;
      });
        
      html += `<span class="cd-words-wrapper">`;
      data.Words.forEach((word, index) => {
        if (index == 0) {
          html += `<b class="is-visible">${word}</b>`
        } else {
          html += `<b> ${word}</b>`;
        }

      })
      html += `</span></div>`;
      Section.innerHTML = html;
    });
    Section.innerHTML = Loader.SmallLoader();
  }
}