import { API } from '../../API';
//import Loader from '../Loader/Loader';

export default class Academics {
  static LoadAcademics() {
    const academicsId = document.getElementById("academics");
    const URL = "https://shubhamjagdhane.github.io/jsonfiles/pucsd/Academics/academics.json";
    let html = ``;
    
    API.get(URL)
    .then((data) => {
      html += 
      `
      <div class="section-title text-center">
        <h3>${data.Heading}</h3>    
      </div>;
      `;
      //html += `<div class="main-section">`;
      html += `<div class="main-section"></div>`;
      academicsId.innerHTML = html;

      const academicsInfo = document.querySelector("#academics .main-section");
      
      html = ``;
      
      data.Courses.forEach((course) => {
        html += 
        `
        <div class="post-box">
        <div class="post-thumb">
          <img src="${course.Image}" class="img-fluid" alt="post-img" />
          <div class="date">
            <span>${course.Years}</span>
            <span>Years Course</span>
          </div> 
        </div>
        <div class="post-info">
          <h4>${course.Course}</h4>
          <p>${course.Description}</p>
        </div>
      </div>
        `
      });
      
      academicsInfo.innerHTML = html;
      
    })

    //academicsId.innerHTML = Loader.SmallLoader();

  }
  static LoadContent() {
    setTimeout(() => {
      const ISOptions = {
        threshold: 0.8,
        rootMargin: "0px 0px 0px 0px"
      }
      const academicsObserver = new IntersectionObserver((entries, academicsObserver) => {
        entries.forEach((entry)=> {
          if(entry.isIntersecting) {
            academicsObserver.unobserve(entry.target);
            Academics.LoadAcademics();
          }
        });
        
      }, ISOptions);
      academicsObserver.observe(document.getElementById("academics"));
    }, 2000);
  }
}