
export default class HeaderOperations {

  static LoadHeaderOperations() {
    HeaderOperations.ChangeOnScroll();
    HeaderOperations.AddActiveClassOnScroll();
  }

  static ChangeOnScroll() {
    const sectionOneOptions = {
      rootMargin: "-450px 0px 0px 0px"
    };

    const sectionOneObserver = new IntersectionObserver(function(entries, sectionOneObserver){
      
      setTimeout(() => {
        var headerOps = document.querySelector(".header");
        if (headerOps == null)
          return
        entries.forEach(entry => {
          if (!entry.isIntersecting) {
            headerOps.classList.add("header-scrolled");          
          } else {
            headerOps.classList.remove("header-scrolled");      
          }              
        });
      },
      500);
      
      
    }, sectionOneOptions);
    const navbar = document.querySelector(".navbar");
    sectionOneObserver.observe(navbar);
    
    HeaderOperations.ScrollSmoothly();
  }

  static ScrollSmoothly() {
    setTimeout(()=>{
      const navbarLinks = document.querySelectorAll("#home ul li a");
      navbarLinks.forEach(link => {
        link.addEventListener("click", (e) => {
          const element = document.getElementById(link.getAttribute("data-link").substring(1));
          HeaderOperations.AddActiveClassOnClick(link);
  
          element.scrollIntoView({behavior:"smooth", block: "start"});
          
          document.getElementById("menu-btn").checked = false;
        });
      })  
    },500);
    
    
  }

  

  static AddActiveClassOnClick(link) {
    setTimeout(() => {
      const currentActive = document.getElementsByClassName("active")[0];
      currentActive.classList.remove("active");
      link.classList.add("active");
    }, 1000)    
    
  }

  static AddActiveClassOnScroll() {
    const sections = document.querySelectorAll("section");
    const sectionOptions = {
      rootMargin: "0px 0px 0px 0px",
      threshold: 0.2
    };
  
    const observer = new IntersectionObserver(HeaderOperations.NavCheck, sectionOptions);    
   
    sections.forEach(section => {
      observer.observe(section);
    })
  }

  static NavCheck(entries) {
    setTimeout(()=> {
      entries.forEach(entry => {

        if (!entry.isIntersecting) {
          return
        }
        else {
          const a_tag = "[data-link='#" + entry.target.id + "']"; 
          const link = document.querySelector(a_tag);
          const currentActive = document.getElementsByClassName("active")[0];
          if(currentActive != null)
            currentActive.classList.remove("active");
          if (link != null)
            link.classList.add("active");
         
        }
  
      });
    },500)
  }

}