import { API } from '../../API';
//import Loader from '../Loader/Loader';

export default class Faculty {
  static LoadFaculty() {
    const URL = "https://shubhamjagdhane.github.io/jsonfiles/pucsd/Faculty/faculty.json";
    let html = ``;
    const facultyId = document.getElementById("faculty");

    API.get(URL)
    .then((data) => {
      html += 
      `
      <div class="section-title text-center">
        <h3>${data.Heading}</h3>    
      </div>
      ` ;
      html += `<div class="main-section"></div>`; 
      facultyId.innerHTML = html;
      const facultyInfo = document.querySelector("#faculty .main-section");
      html = ``;

      data.Staff.Teaching.forEach((teacher) => {
        html += 
        `
        <div class="our-team">
        <div class="pic">
          <img src="${teacher.Image}" alt="Image">
        </div>
        <div class="team-content"><h3 class="title">`;

        
        if (teacher.Name.Prefix != "") {
          html += teacher.Name.Prefix + ". ";
        }
        html += teacher.Name.First + (teacher.Name.Middle ? " " + teacher.Name.Middle + ". ":  " ") + teacher.Name.Last;
          
        html +=  `</h3>
          <span class="post">
            <ul>
              <li>${teacher.Post}</li>`;
            
        if (teacher.Qualification != "") {
          html += `<li>${teacher.Qualification}</li>`;
        }
        
        html +=
          `  <li>
                <div class="date">
                  <span><a target="_blank" href="${teacher.Webpage}"><i class="fa fa-external-link-square" aria-hidden="true"></i></a></span>
                </div>
              </li>
            </ul>
          </span>
        </div>
      </div>
        `;        
      })
      facultyInfo.innerHTML = html;
    })

    //facultyId.innerHTML = Loader.SmallLoader();
  }

  static LoadContent() {
    setTimeout(() => {
      const ISOptions = {
        threshold: 0.8,
        rootMargin: "0px 0px 0px 0px"
      }
      const facultyObserver = new IntersectionObserver((entries, facultyObserver) => {
        entries.forEach((entry)=> {
          if(entry.isIntersecting) {
            facultyObserver.unobserve(entry.target);
            Faculty.LoadFaculty();
          }
        });
        
      }, ISOptions);
      facultyObserver.observe(document.getElementById("faculty"));
    }, 2000);
  }
}