import { API } from '../../API';
//import Loader from '../Loader/Loader';

export default class About {
  static LoadAbout() {
    const URL = "https://shubhamjagdhane.github.io/jsonfiles/pucsd/About/about.json";
    const aboutId = document.getElementById("about");
    API.get(URL)
    .then(data => {
      
      var html = `
      <div class="demo">
        <div class="message-box">
          <h3>${data.About.Heading}</h3>`;

          data.About.Information.forEach((info) => {
            html += `<p>${info}</p>`;
          });
          
        html += `</div>
        <div class="message-image float">
          <img src="${data.About.Image}" alt="About" />
          <div class="black"></div>
        </div>
      </div>`;


      html += 
      `
      <div class="hod-message message-box">
      <h3>${data.HOD.Heading}</h3>
      `;

      data.HOD.Information.forEach((paragraph) => {
        html += `<p>${paragraph}</p>`;
      })

      
      html += `</div>`

      aboutId.innerHTML = html;

    });
    //aboutId.innerHTML = Loader.SmallLoader();
  }

  static LoadContent() {
    setTimeout(() => {
      const ISOptions = {
        threshold: 0.8,
        rootMargin: "0px 0px 0px 0px"
      }
      const aboutObserver = new IntersectionObserver((entries, aboutObserver) => {
        entries.forEach((entry)=> {
          if(entry.isIntersecting) {            
            aboutObserver.unobserve(entry.target);
            About.LoadAbout();
          }
        });
        
      }, ISOptions);
      aboutObserver.observe(document.getElementById("about"));
    }, 2000);
  }
}