import {API} from '../../API';
//import Loader from '../Loader/Loader';

export default class Header {
  constructor(){
      this.URL = "https://shubhamjagdhane.github.io/jsonfiles/pucsd/Headers/";
      this.sectionId = "home";
  }

  LoadData() {
     
    const URL = this.URL + "headers.json";
    const headerDOM = document.getElementById(this.sectionId);     
    
    let html = ``;
    API.get(URL)
    .then(header => {
      html = `<div class="overlay"></div> <header class="header"> <a href="#"><div class="logo"><img src="${header.Logo}" alt="Logo"/><h3>${header.CompanyName.toUpperCase()}</h3></div></a><input class="menu-btn" type="checkbox" id="menu-btn" /><label class="menu-icon" for="menu-btn"><span class="nav-icon"></span></label><ul class="menu">`;

      header.NavBarList.forEach(link => {
        const LinkName = link[0].toUpperCase() + link.slice(1)
        html += `<li><a data-link="#${link}">${LinkName}</a></li>`
        
      });    

      html +=  `</ul></header>`;
      
      headerDOM.innerHTML = html; 
             
    });
        
    //headerDOM.innerHTML = Loader.LargeLoader();

  }
}