import { API } from '../../API';
//import Loader from '../Loader/Loader';

export default class Contact {
  static LoadContacts() {
    const URL = "https://shubhamjagdhane.github.io/jsonfiles/pucsd/Contact/contact.json";
    const contactId = document.getElementById("contact");
    let html = ``;
    API.get(URL)
    .then((contact) => {
      html += 
      `
      <div class="section-title text-center">
        <h3>${contact.Heading}</h3>    
      </div>;
      `;
      //html += `<div class="main-section">`;
      html += `<div class="grid-col-2 decorate"></div>`;
      contactId.innerHTML = html;

      const contactInfo = document.querySelector("#contact .decorate");
      
      html = ``;
      
      contact.Information.forEach((of) => {
        html += 
        `
        <div>
          <ul>
            <li><h2>${of.Queries}</h2></li>
            <li>
              <table>
                <tr>
                  <td><h3>Working Days: </h3></td>
                  <td><h3>${of.WorkingDays}</h3></td>
                </tr>

                <tr>
                  <td><h3><i class="fa fa-clock-o" aria-hidden="true"></i><h3></td>
                  <td><h3>${of.Time}</h3></td>
                </tr>

                <tr>
                  <td><h3><i class="fa fa-phone-square" aria-hidden="true"></i></h3></td>
                  <td><h3>${of.Phone}</h3></td>
                </tr>
                <tr>
                  <td><h3><i class="fa fa-envelope" aria-hidden="true"></i></h3></td>
                  <td><h3><a href="mailto:${of.Email}">${of.Email}</a></h3></td>
                </tr>
              </table>
            </li>
          </ul>
        </div>
        `
      });
      
      contactInfo.innerHTML = html;
    });
    //contactId.innerHTML = Loader.SmallLoader();
  }
  
  static LoadContent() {
    setTimeout(() => {
      const ISOptions = {
        threshold: 0.8,
        rootMargin: "0px 0px 0px 0px"
      }
      const contactObserver = new IntersectionObserver((entries, contactObserver) => {
        entries.forEach((entry)=> {
          if(entry.isIntersecting) {
            contactObserver.unobserve(entry.target);
            setTimeout(Contact.LoadContacts(), 10000);
          }
        });
        
      }, ISOptions);
      contactObserver.observe(document.getElementById("contact"));
    }, 2000);
  }
}